.form{
    background-color: white;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.form_logo{
    margin-top: 20px;
    margin-bottom: 20px;
    object-fit: contain;
    width: 100px;
    margin-right: auto;
    margin-left: auto;
}

.form_container{
    width: 300px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border: 1px solid lightgray;
    padding: 20px;
}

.form_container > h1{
    font-weight: 500;
    margin-bottom: 20px;
}

.form_container > form >h5{
    margin-bottom: 5px;
}

.form_container > form >input{
    height: 30px;
    margin-bottom: 10px;
    background-color: white;
    width: 98%;
}

.form_container > form >textarea{
    height: 90px;
    margin-bottom: 10px;
    background-color: white;
    width: 98%;
}

.submit_FormButton{
    background: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
}